    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.header {
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 20px;

    display: flex;
    flex-direction: column;
    padding-left: 0px;
    padding-right: 0px;
    padding-top: 20px;
    padding-bottom: 20px;
    flex-wrap: nowrap;
}

.header .libraryMenuItem {
    color: white;
    padding: 20px;
    margin: 0;
    text-wrap: nowrap;
    cursor: pointer;
    display: flex;
    gap: 20px;
    justify-content: space-between;
}

.header .libraryMenuItem.selected .libraryName {
    color: aquamarine;
}

.header .libraryMenuItem .editIcon {
    opacity: 0;
    transition: all 0.4s;
}

.header .libraryMenuItem:hover .editIcon {
    opacity: 0.5;
}

.header .libraryMenuItem .editIcon:hover {
    opacity: 1;
}

.header .addIcon {
    color: white;
    cursor: pointer;
}`, "",{"version":3,"sources":["webpack://./src/MovieOverview.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,mBAAmB;IACnB,eAAe;AACnB;;AAEA;IACI,oCAAoC;IACpC,mBAAmB;;IAEnB,aAAa;IACb,sBAAsB;IACtB,iBAAiB;IACjB,kBAAkB;IAClB,iBAAiB;IACjB,oBAAoB;IACpB,iBAAiB;AACrB;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,SAAS;IACT,iBAAiB;IACjB,eAAe;IACf,aAAa;IACb,SAAS;IACT,8BAA8B;AAClC;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,UAAU;IACV,oBAAoB;AACxB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,YAAY;IACZ,eAAe;AACnB","sourcesContent":[".movieOverview {\n    display: flex;\n    flex-direction: row;\n    flex-wrap: wrap;\n}\n\n.header {\n    background-color: rgba(0, 0, 0, 0.5);\n    border-radius: 20px;\n\n    display: flex;\n    flex-direction: column;\n    padding-left: 0px;\n    padding-right: 0px;\n    padding-top: 20px;\n    padding-bottom: 20px;\n    flex-wrap: nowrap;\n}\n\n.header .libraryMenuItem {\n    color: white;\n    padding: 20px;\n    margin: 0;\n    text-wrap: nowrap;\n    cursor: pointer;\n    display: flex;\n    gap: 20px;\n    justify-content: space-between;\n}\n\n.header .libraryMenuItem.selected .libraryName {\n    color: aquamarine;\n}\n\n.header .libraryMenuItem .editIcon {\n    opacity: 0;\n    transition: all 0.4s;\n}\n\n.header .libraryMenuItem:hover .editIcon {\n    opacity: 0.5;\n}\n\n.header .libraryMenuItem .editIcon:hover {\n    opacity: 1;\n}\n\n.header .addIcon {\n    color: white;\n    cursor: pointer;\n}"],"sourceRoot":""}]);
