body {
    font-family: Arial, sans-serif;
    padding: 2rem;
    background-color: #1a1f24;
}

.contentWrapper {
    display: flex;

}

input[type=text] {
    background-color: black;
    outline: none;
    border: rgba(255, 255, 255, 0.33) solid 1px;
    border-radius: 10px;
    padding: 10px;
    color: white;
    font-size: 15px;
    min-width: 300px;
}

input[type=text]:focus {
    border-color: aquamarine;
}

button {
    background: rgba(128, 255, 212, 0.33);
    outline: none;
    border: aquamarine solid 1px;
    border-radius: 10px;
    padding: 10px;
    color: white;
    font-size: 15px;
    transition: all 0.4s;
    font-weight: bold;
    cursor: pointer;
}

button:disabled {
    background: rgba(128, 128, 128, 0.33);
}

button:hover {
    background: rgba(128, 255, 212, 1);
    color: black;
}

button:disabled:hover {
    background: rgba(128, 128, 128, 0.33);
    color: white;
    cursor: auto;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA,mBAAmB;AACnB;IACI,8BAA8B;IAC9B,aAAa;IACb,yBAAyB;AAC7B;;AAEA;IACI,aAAa;;AAEjB;;AAEA;IACI,uBAAuB;IACvB,aAAa;IACb,2CAA2C;IAC3C,mBAAmB;IACnB,aAAa;IACb,YAAY;IACZ,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,wBAAwB;AAC5B;;AAEA;IACI,qCAAqC;IACrC,aAAa;IACb,4BAA4B;IAC5B,mBAAmB;IACnB,aAAa;IACb,YAAY;IACZ,eAAe;IACf,oBAAoB;IACpB,iBAAiB;IACjB,eAAe;AACnB;;AAEA;IACI,qCAAqC;AACzC;;AAEA;IACI,kCAAkC;IAClC,YAAY;AAChB;;AAEA;IACI,qCAAqC;IACrC,YAAY;IACZ,YAAY;AAChB","sourcesContent":["/* src/styles.css */\nbody {\n    font-family: Arial, sans-serif;\n    padding: 2rem;\n    background-color: #1a1f24;\n}\n\n.contentWrapper {\n    display: flex;\n\n}\n\ninput[type=text] {\n    background-color: black;\n    outline: none;\n    border: rgba(255, 255, 255, 0.33) solid 1px;\n    border-radius: 10px;\n    padding: 10px;\n    color: white;\n    font-size: 15px;\n    min-width: 300px;\n}\n\ninput[type=text]:focus {\n    border-color: aquamarine;\n}\n\nbutton {\n    background: rgba(128, 255, 212, 0.33);\n    outline: none;\n    border: aquamarine solid 1px;\n    border-radius: 10px;\n    padding: 10px;\n    color: white;\n    font-size: 15px;\n    transition: all 0.4s;\n    font-weight: bold;\n    cursor: pointer;\n}\n\nbutton:disabled {\n    background: rgba(128, 128, 128, 0.33);\n}\n\nbutton:hover {\n    background: rgba(128, 255, 212, 1);\n    color: black;\n}\n\nbutton:disabled:hover {\n    background: rgba(128, 128, 128, 0.33);\n    color: white;\n    cursor: auto;\n}"],"sourceRoot":""}]);
